.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.containerdiv {
    border: 0;
    float: left;
    position: relative;
  } 
.cornerimage {
    border: 0;
    position: absolute;
    top: 0;
    right: 0;
} 

.anchor_center {
    transform-origin: 50% 100%;
}

.map_text {
    color: #FFFFFF;
    border: #000000;
    border-width: 5;
    white-space: nowrap;
    text-align: center;
    margin: .3em 0 0;
    color: white;
    font-size: 20px;
    text-shadow: 2px 2px 0 #000000;
    display: flex;
    justify-content: center;
}

.invisible {
    background: #00ff0000;
}

#map { flex: 1 1 auto }
